import React, { Component } from 'react';
import Loading from './loading.gif'

export default class TicTacClick extends Component {
	getWinner = (values) => {
		const winMap = [
			[0, 1, 2],
			[0, 3, 6],
			[0, 4, 8],
			[1, 4, 7],
			[2, 4, 6],
			[2, 5, 8],
			[3, 4, 5],
			[6, 7, 8],
		];
		for (let i = 0; i < winMap.length; i++) {
			const [a, b, c] = winMap[i];
			if (values[a] && values[a] === values[b] && values[a] === values[c]) {
				return values[a];
			}
		}
		return false;
	};
	state = {
		values: new Array(9).fill(null),
		whichIsNext: false,
		histories: [],
		stepNumber: 0,
		firstPlayer: '1st',
    secondPlayer: '2nd', 
    modalOpen: false,
    loading:false
  };
  
  toggleModal=()=>this.setState({modalOpen:!this.state.modalOpen})

	clickHandler = (index) => {
		const { firstPlayer, secondPlayer } = this.state;
		const histories = this.state.histories.slice(0, this.state.stepNumber + 1);
		const values = this.state.values;
		if (values[index]) {
			return;
		}
		values[index] = this.state.whichIsNext ?   secondPlayer : firstPlayer
		this.setState({
			whichIsNext: !this.state.whichIsNext,
			stepNumber: histories.length,
			histories: histories.concat([[...values]]),
		});
		if (values.every((value) => value !== null)) {
			this.setState({
				gameHeader: 'Mission Failed!',
			});
		}
	};

	jumpValues = (values, index) => {
    this.setState({
      values, stepNumber: index, whichIsNext: index % 2 === 0
    });
	};

	resetGame = () => {
		this.setState({
			values: new Array(9).fill(null),
			whichIsNext: false,
			histories: [],
			stepNumber: 0,
			firstPlayer: '1st',
			secondPlayer: '2nd',
		});
  };
  
  changePlayerName = (e) => {
    this.setState({
      [e.target.id]:e.target.value
    })
  }

  submit = (e) => { 
    e.preventDefault();
    this.setState({ loading: true }) 
    setTimeout(() => { 
      this.setState({loading:false, modalOpen:false})
    },900)
  }

	render() {
		const { values, histories } = this.state;
		const winner = this.getWinner(values);

		return (
			<div
				style={{ background: '#000000', width: '100%', minHeight: '100vh', maxHeight: 'fit-content' }}
				className="d-flex justify-content-center align-items-center" >
				<div className="col-md-12">
					<div className="row text-light">
						<div className="col-md-9 my-3 text-center">
							<button disabled={true} className="btn btn-dark btn-sm m-3">
								{winner ? 'Winner (' + winner + ') ✔ ' : this.state.whichIsNext ? 'Next Player is '+ this.state.secondPlayer : 'Next Player is ' + this.state.firstPlayer }
              </button>
              <button onClick={this.toggleModal} className="btn btn-primary btn-sm m-3"> 🔠 Set Player Name</button>
							<div
								style={{
									display: 'grid',
									justifyContent: 'center',
									alignContent: 'center',
									gridTemplateColumns: 'repeat(3,110px)',
									gridTemplateRows: 'repeat(3,110px)',
								}}
							>
								{values.map((value, index) => (
									<button
                  style={{wordWrap:'break-word'}}
										onClick={() => this.clickHandler(index)}
										key={index}
										disabled={value || winner}
										className="btn btn-dark m-1 rounded-0"
									>
										{winner === value && '✔'}{' '}
										{value ? value : <small className="text-info">click</small>}
									</button>
								))}
							</div>
						</div>
						<div className="col-md-3 my-3">
							<button onClick={this.resetGame} className="btn btn-danger btn-sm btn-block">
								Reset Game
							</button>
							{histories.map((values, index) => (
								<button
									onClick={() => this.jumpValues(values.slice(), index)}
									key={index}
									className="btn btn-dark btn-sm btn-block"
								>
									Back to ({index})
								</button>
							))}
						</div>
					</div>
        </div>
        
      {this.state.modalOpen && (            
        <div style={{background:'#000000ee'}} className="modal d-block">
              <div className="modal-dialog">
            <div className="modal-content bg-dark text-light">
              <div className="modal-header py-1">
                <h6 className="modal-title text-info">Set Player Name</h6>
                <button onClick={this.toggleModal} className="btn btn-danger btn-sm px-4">&times;</button>
              </div>
                <div className="modal-body"> 
                  <form onSubmit={this.submit}>
                  <label htmlFor="firstPlayer" className="m-0">First Player</label>
                  <input onChange={this.changePlayerName} value={this.state.firstPlayer} id="firstPlayer" placeholder="First Player Name..." type="text" className="form-control form-control-sm" />
                  <label htmlFor="secondPlayer" className="m-0 mt-3">Second Player</label>
                  <input onChange={this.changePlayerName} value={this.state.secondPlayer} id="secondPlayer" placeholder="Second Player Name..." type="text" className="form-control form-control-sm" />
                  <button className="btn btn-primary btn-block btn-sm mt-3">Update Player Name 🏈 </button> 
                  </form>
              </div>
            </div>
          </div>
          </div>)}
        
          {this.state.loading && <div className="modal d-block d-flex justify-content-center align-items-center">
          <img className="w-25" src={Loading} alt="" />
        </div>}

			</div>
		);
	}
}
